import React from 'react';
import Navbar from '../../components/Navbar'
import '../index.css';

const EnPrivacyPolicy = () => {
    return (
        <>
          <div className="article-navbar">
            <Navbar />
          </div>  
          <div className="article">
          <div className="container white z-depth-3">
            <h2 style={{ marginBlockStart:0, paddingTop: "5vh"}}>Politica sulla riservatezza</h2>
            <p className="flow-text">
                Questa politica sulla privacy spiega come “IG Tracker” (“Applicazione”, “Servizio”) utilizza e protegge tutte le informazioni fornite all'app quando viene utilizzata. L'informativa sulla privacy può subire modifiche sporadiche e si rifletterà in questa pagina. Dovresti controllare questa pagina di tanto in tanto per assicurarti di essere soddisfatto di eventuali modifiche.
            </p>
            <h3>Quali dati raccogliamo</h3>
            <p className="flow-text">
                L'applicazione non ottiene alcun tipo di informazione personale dall'utente. Tutta l'elaborazione dei dati, come il calcolo metrico o il rilevamento delle modifiche, viene eseguita sul dispositivo dell'utente. Tutte le informazioni disponibili nell'applicazione vengono raccolte dall'applicazione direttamente da Internet e l'applicazione non condivide i dati ottenuti con alcun tipo di servizio esterno (Né servizi propri né applicazioni di terze parti).
            </p>
            <p className="flow-text">
                L'applicazione ottiene solo informazioni anonime allo scopo di pubblicità mirata e tracciamento del comportamento dell'utente all'interno dell'applicazione. Se l'utente desidera eliminare il diritto di accesso a queste informazioni ottenute automaticamente, può disattivare il monitoraggio individuale del dispositivo nelle impostazioni del proprio telefono o tablet. Queste informazioni personali non private includono, ma non sono limitate a, informazioni sul dispositivo, sistema operativo e indirizzo IP..
            </p>
            <p className="flow-text">
                Non vendiamo o condividiamo informazioni ottenute con terze parti al di fuori delle operazioni commerciali di IG Tracker.
            </p>
            <h3>Che tipo di informazioni mostra l'app?</h3>
            <p className="flow-text">
            L'applicazione è un servizio di informazioni e non garantiamo di darti il ​​100% di accuratezza nei risultati. L'applicazione si basa sulla disponibilità di alcuni servizi pubblici su Internet e non siamo responsabili di alcun tipo di malfunzionamento dovuto all'indisponibilità di questi servizi. Accetti e sei d'accordo con questo quando scarichi e utilizzi l'applicazione.
            </p>
            <h3>Cosa facciamo con le informazioni che otteniamo?</h3>
            <p className="flow-text">
                Richiediamo la raccolta di informazioni personali non private per comprendere le esigenze dei nostri utenti e fornire loro un servizio migliore, in particolare per i seguenti motivi:
            </p>
            <ul className="flow-text">
              <li>Esperienza utente</li>
              <li>Informazioni per migliorare i nostri prodotti e servizi</li>
            </ul>
            <p className="flow-text">
                Ci impegniamo a garantire che le tue informazioni siano al sicuro e ci affidiamo a servizi noti e sicuri come Google Analytics per ottenere questi dati non personali.
            </p>
            <h3>Collegamenti ad altre pagine</h3>
            <p className="flow-text">
                La nostra applicazione può contenere collegamenti ad altri contenuti di interesse. Tuttavia, una volta che hai utilizzato questi collegamenti per lasciare la nostra applicazione, tieni presente che non abbiamo alcun controllo sul contenuto. In questo modo, non possiamo essere responsabili per la protezione e la privacy delle informazioni fornite durante la visita a uno di questi siti, che non sono regolati dalla presente informativa sulla privacy. È necessario prestare attenzione quando si accede a questo contenuto e consultare l'informativa sulla privacy del sito Web in questione.
            </p>
            <h3>Come utilizziamo i cookie</h3>
            <p className="flow-text">
                Un cookie è una piccola informazione di testo memorizzata nella memoria del tuo dispositivo. Aiuta l'applicazione a ricordare le informazioni sulla tua visita e consente alla nostra applicazione di ricordare le informazioni sull'uso che ne fai. L'applicazione può adattare le sue operazioni alle tue esigenze e preferenze raccogliendo e ricordando le informazioni sulle tue preferenze. Utilizziamo cookie di traffico per identificare le finestre utilizzate. Questo ci aiuta ad analizzare i dati sul traffico delle applicazioni e a migliorare la nostra applicazione per soddisfare le esigenze dei nostri clienti. Utilizziamo queste informazioni solo per scopi di analisi statistica..
            </p>
            <p className="flow-text">
                In generale, i cookie ci aiutano a fornire un servizio migliore, consentendo il monitoraggio di quali finestre trovi utili e quali no. Un cookie non ci dà in alcun modo il permesso di accedere al tuo dispositivo oa qualsiasi tipo di informazione su di te diversa dai dati che hai accettato di condividere con noi.
            </p>
            <h3>Le nostre garanzie e esclusioni di responsabilità</h3>
            <p className="flow-text">
            Non siamo affiliati a Instagram e non memorizziamo alcun tipo di contenuto di Instagram su nessuno dei nostri servizi. Tutto il contenuto visualizzato dall'applicazione viene memorizzato sul dispositivo dell'utente e tutti i diritti appartengono ai rispettivi autori. La nostra applicazione contiene collegamenti a siti Web mantenuti e gestiti da terze parti. Se utilizzi questi link, esci dall'applicazione.
            </p>
            <p className="flow-text">
                Questi collegamenti sono forniti nel caso in cui siano di interesse per l'utente e il contenuto non sia approvato da IG Tracker. IG Tracker non ha alcun controllo sui contenuti di alcun sito Web collegato e non è responsabile di tali contenuti. IG Tracker, inoltre, non rilascia alcun tipo di garanzia sulle informazioni dei siti collegati.
            </p>
            <p className="flow-text">
                I presenti Termini di utilizzo descrivono i termini e le condizioni legalmente vincolanti che monitorano l'utilizzo dell'Applicazione. UTILIZZANDO QUESTA APPLICAZIONE, SIETE VINCOLATI DA QUESTI TERMINI e dichiarate di avere l'autorità e la capacità di accettare questi Termini.
            </p>
            <h3>Quali sono i nostri servizi pubblicitari?</h3>
            <h4>Google Admob</h4>
            <p className="flow-text">
                Questa applicazione utilizza Google admob, una piattaforma di pubblicità per dispositivi mobili creata da Google. Admob è una delle più grandi piattaforme pubblicitarie al mondo e afferma di servire più di 40 miliardi di banner e annunci di testo al mese su siti Web e applicazioni mobili. Puoi leggere la loro politica sulla privacy <a rel="noreferrer,nofollow,noindex" href="https://support.google.com/admob/answer/6128543" target="_blank">qui.</a>
            </p>
            <h4>Google FireBase</h4>
            <p className="flow-text">
            Firebase fornisce funzionalità come analisi, database, messaggistica e segnalazione di bug in un modo che ti consente di andare avanti rapidamente e concentrarti sui tuoi utenti. Puoi leggere di più sulla loro politica sulla privacy <a rel="noreferrer,nofollow,noindex" href="https://firebase.google.com/support/privacy" target="_blank">qui.</a>
            </p>
            <h3>Quali sono i miei diritti di rinuncia?</h3>
            <p className="flow-text">
            Sono disponibili più opzioni di disattivazione per gli utenti delle nostre applicazioni e giochi:  
            </p>
            <p className="flow-text">
                Scegli di interrompere la raccolta di informazioni disinstallando l'applicazione: Puoi interrompere facilmente tutta la raccolta di informazioni dalle nostre app e dai nostri giochi disinstallandoli. È possibile utilizzare i processi di disinstallazione standard che potrebbero essere disponibili come parte del dispositivo mobile o tramite il marketplace o la rete di applicazioni mobili.
            </p>
            <p className="flow-text">
            Scegliere di non utilizzare le informazioni per servire la pubblicità mirata di inserzionisti e/o inserzionisti nella rete di terze parti: in qualsiasi momento puoi scegliere di non consentirci più l'accesso ai dati sulla tua posizione nei seguenti modi:
            </p>
            <h4>Segui direttamente le istruzioni dei servizi di terze parti:</h4>
            <p className="flow-text">
            Per Google (AdMob) visita <a rel="noreferrer,nofollow,noindex" href="https://www.google.com/ads/preferences/html/mobile-about.html" target="_blank">questa pagina web.</a>
            </p>
            <h4>Attivare le impostazioni "Non tracciare" sul dispositivo mobile</h4>
            <p className="flow-text">
            I fornitori di servizi pubblicitari sono tenuti ad aderire all'impostazione "Non tracciare" sui telefoni cellulari, ma non possiamo fornire alcuna garanzia per loro conto. Non offriamo alcuna garanzia rispetto alle informazioni fornite prima dell'esclusione. Alcuni provider potrebbero disporre di meccanismi di opt-out aggiuntivi.
            </p>
            <p className="flow-text">
            Se attivi "Limitazione della pubblicità" sul tuo dispositivo iOS o "Disattiva annunci basati sugli interessi" sul tuo dispositivo Android, puoi reimpostare gli annunci basati sugli interessi disabilitando le impostazioni. Quando è disattivato, potresti ancora vedere lo stesso numero di annunci sul tuo dispositivo mobile; tuttavia, questi annunci potrebbero essere meno pertinenti perché non saranno basati sui tuoi interessi. I nostri partner pubblicitari possono continuare a fornirti annunci pubblicitari basati su altre informazioni non personali, come annunci pubblicitari relativi al contenuto dell'applicazione che stai utilizzando.
            </p>
            <p className="flow-text">
            Il tuo dispositivo mobile iOS o Android potrebbe fornire una configurazione di "Limitazione della pubblicità" o "Disattiva annunci basati sugli interessi". Quando hai scelto di non utilizzare questa impostazione su un dispositivo, i nostri partner non utilizzeranno le informazioni raccolte da quel dispositivo per dedurre i tuoi interessi o per servire annunci pubblicitari mirati in base ai tuoi interessi dedotti su quel dispositivo. È possibile trovare le impostazioni per il proprio dispositivo mobile seguendo le istruzioni di seguito o contattare il provider del dispositivo.
            </p>
            <p className="flow-text">
            Il motivo per passare dall'identificazione del dispositivo all'identificazione della pubblicità è che gli inserzionisti possono utilizzare questo identificatore solo per la pubblicità e per nessun altro scopo, mentre gli utenti possono scegliere di non tracciare impostazioni del telefono cellulare. L'identificatore pubblicitario è un identificatore anonimo per scopi pubblicitari che consente agli utenti di reimpostare il proprio identificativo o disattivare gli annunci basati sugli interessi. Le parti coinvolte nella pubblicità sono obbligate a seguire la decisione degli utenti di rinunciare alla scansione secondo le linee guida dell'app store..
            </p>
            <p className="flow-text" style={{paddingBottom:"4vh"}}>
            Per i dispositivi Android con OS 2.2 e versioni successive e versione 4.0 di Google Play Services e versioni successive: Apri la tua app Google Settings {'>'} Annunci  {'>'} Abilita l'opzione "Disattiva Personalizzazione annunci".
            </p>
          </div>
          </div>
        </>
    );
}

export default EnPrivacyPolicy;